@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-top: 20px;
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
}

.background {
  background: url("https://i.imgur.com/gb0Mznv.png") no-repeat center center
    fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
}

.bottom {
  margin: auto;
  display: flex;
  width: 80%;
  align-items: center;
  background-color: blue;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotateY(45deg);
            transform: rotateY(45deg);
  }
  to {
    -webkit-transform: rotateY(225deg);
            transform: rotateY(225deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotateY(45deg);
            transform: rotateY(45deg);
  }
  to {
    -webkit-transform: rotateY(225deg);
            transform: rotateY(225deg);
  }
}

.square {
  margin: auto;
  display: flex;
  width: 80%;
  align-items: center;
  background-color: #f5f3f3;
  border-radius: 5px;
  border-style: solid;
  border-width: 5px;
}
.text {
  margin: auto;
  text-align: center;
  font-family: Gaegu;
  font-size: 2vw;
  padding: 30px;
}

nav {
  position: fixed;
  top: 0;
  width: 100%;
  color: black;
  background-color: #c7c7c7;
}

h1 {
  font-family: Gaegu;
  color: white;
  font-size: 25px;
}

h1:hover {
  color: #ff00ff;
}

a {
  font-family: Gaegu;
  font-size: 25px;
  color: #c7c7c7;
}

@media screen and (max-width: 500px) {
  .list {
    flex-direction: column;
    height: auto;
  }
  .items:nth-child(1) {
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    margin-top: 50px;
  }
  .items {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    text-align: center;
    margin-right: 0px;
    padding: 20px 0;
  }
  .btn {
    display: block;
  }
}

